<template>
	<div class="filmtype">
		<div  class="classic clearfix">
			<span v-for='val in classic'>{{val}}</span>
		</div>
		<div  class="plot clearfix">
			<span v-for='val in plot'>{{val}}</span>
		</div>
		<div  class="abroad clearfix">
			<span v-for='val in abroad'>{{val}}</span>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
				"classic":["经典","豆瓣高分","冷门佳片"],
				"plot":["剧情","爱情","喜剧","科幻","动作","悬疑","治愈","青春","文艺"],
				"abroad":["日本","韩国","美国"]

			}
		}
	}
</script>
<style>
	.filmtype{margin-bottom:.3rem}

	.classic span,.plot span,.abroad span{float: left;font-size: .34rem;padding:.1rem .5rem;display: inline-block;color:#70CD80;border: 1px solid #70CD80;border-radius:.5rem;margin:.3rem .1rem}
	.classic,.plot,.abroad{margin:.25rem;border-bottom:1px solid #F7F7F7;padding:0.25rem 0rem}
	.filmtype div:last-child{border-bottom: none}
</style>


