<template>
	<div class="hjgsortwrap clearfix">
		<div class="sortlist">
			<div class="item" :class={hot:item.ishot} v-for='(item,i) in arrlist'><span class="indexnum">{{i+1}}.</span><span class="tvname">{{item.tvname}}</span><span class="tvnum">{{item.num}}</span>
			</div>
			
		</div>
	</div>
</template>

<script type="text/javascript">
	export default{
		props:{
			"arrlist":{
				type:Array,
				default:[]
			}
		}
	}
</script>

<style type="text/css">
.hjgsortwrap{padding:0 0.6rem}
.hjgsortwrap .sortlist .item{font-size:.35rem;float: none;width:10rem;height:.5rem;width: 11rem;height:.6rem;line-height:.5rem;padding:.15rem 0}
.hjgsortwrap .sortlist .indexnum{margin-right:.3rem;}
.hjgsortwrap .sortlist .tvnum{float: right;}
.hjgsortwrap .sortlist .hot{color: #FA5097}
</style>


