<template>
  <div class="hnewsWrap" @click="clickFn">
    <div v-for='val in newlist' class="newsList" :class='{newlistfmpg6:fmpg6}' >
      <router-link :to="{name:'Details',params:{id:val.id}}">
      <div class="left">
        <!--标题-->
        <h3>{{val.title}}</h3>
        <p class="top_p">{{val.des}}</p>
        <!--左边下划线-->
        <slot name="underline"></slot>
        <span :class="{underline:isunderline}"></span>
        <!--作者-->
        <p class="bot_lp">{{val.writer}}</p>
        <!--右边文字-->
        <p class="bot_rp" v-show='bol'>{{fromtxt}}</p>
      </div> 
      <img :src="val.urls" :class='{fmpg6:fmpg6}'/>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    props:{
      newlist:{
        type:Array,
        default:[]
      },
      bol:{
        type:Boolean,
        default:false
      },
      fromtxt:{
        type:String,
        default:"来自栏目 :爸爸去哪儿"
      },
      isunderline:{
        type:Boolean,
        default:true
      },
      fmpg6:{
        type:Boolean,
        default:false
      },
      fmpg8:{
        type:Boolean,
        default:false
      }
    },
    methods: {
      clickFn: function () {
        // this.$router.push({name: "Detail", params:{id: this.id}});
      }
    }
  }
</script>

<style scoped>
  .hnewsWrap {position: relative;}
  .hnewsWrap.border_bottom {border-bottom: 1px solid #dfdfdf;margin-bottom: .5rem;}
  .hnewsWrap .newsList{border-bottom: 1px solid #F5F5F5;padding:0.2rem 0;position: relative;}
  .hnewsWrap .left{padding-left: .62rem;width:8rem}
  .hnewsWrap h3 {line-height: .74rem;font-size: .5rem;color: #494949;}
  .hnewsWrap p {line-height: .56rem;color: #9B9B9B;}
  .hnewsWrap .top_p {font-size: .36rem;padding-top: .27rem;width:8rem;}
  /*;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;width:40em*/
  .hnewsWrap .underline {display: block;width: .45rem;height: 1px;background: #CCCCCC;margin: .27rem 0 .24rem;}
  .hnewsWrap .bot_lp {font-size: .38rem;color: #D5D5D5;}
  .hnewsWrap .bot_rp {font-size: .38rem;color: #D5D5D5;position: absolute;right: .6rem;bottom: .25rem;}
  .hnewsWrap img {width: 2.7rem;position: absolute;top: .4rem;right: .6rem;}


/*电影6改动*/
  .hnewsWrap img.fmpg6{height:3.4rem;top:.9rem;}
  .hnewsWrap .newlistfmpg6{border-bottom: none;padding: 1rem 0}
/*电影页面8改动*/
/*  .filmpg8Wrap .hnewsWrap h3{height: 1.5rem}*/
  .filmpg8Wrap .hnewsWrap .newsList{padding:.5rem 0;height:2.5rem}
  .filmpg8Wrap .hnewsWrap img{height:2.5rem;top:.5rem;}
</style>


