<template>
	<div class="broadcastWrap">
		<UserHeader txt='微博'>
			<img src="../../assets/images/ic_status_search_user.png" alt="" class="left" slot="left">
			<img src="../../assets/images/ic_chat_green.png" alt="" class="right" slot="right">
		</UserHeader>

		<BroadBanner titles="发现有趣的人" contents="关注他们，发现更大的世界">
      <img src="../../assets/images/ic_status_anonymous_header.png" slot="pic"/>
   	 </BroadBanner>
		<BroadInner :innerlist='innerlist'></BroadInner> 
		<Repbatch></Repbatch>
	</div>
</template>
<script type="text/javascript">
	import UserHeader from '../../components/usersHeader.vue'
	import BroadBanner from './hjgbroadbanner.vue'
	import BroadInner from './hjgbroadinner.vue'
	import Repbatch from '../../components/hjg_repbatch.vue'
	export default{
		components:{
			UserHeader,BroadBanner,BroadInner,Repbatch
		},
		data(){
			return{
				innerlist:[]
			}
			
		},
		mounted:function(){
			var that = this;
			this.axios.get('/static/data/mybroadcastData.json').then(function(res){
				that.innerlist = res.data.innerlist;
				console.log(res.data.innerlist)
			})
		}
	}
</script>
<style type="text/css">
	.broadcastWrap{background: #F8F8F8}
</style>


