<template>
	<div class="hpage4Wrap clearfix">
		<Hhot title='发现' :bol=true></Hhot>
		<MyBanner swipeid='swipe04' paginationDirection="right" :pagination=false :autoplay=0 :loop=false slidesPerView="auto">
			<div v-for='item in hfindlist'  class="swiper-slide" slot="swiper-con">
			<img :src="item.urls" alt=""/>
			</div>
		</MyBanner>
	</div>	
</template>
<script type="text/javascript">
	import Hhot from '../../components/hjg_hots.vue'
	import MyBanner from './myBanner.vue'
	import Hdbtime from '../../components/hjg_dbtime.vue'
	export default{
		components:{
			Hhot,MyBanner,Hdbtime
		},
		props:{
			hfindlist:{
				type:Array,
				default:[]
			}
		}
	}
</script>


