<template>
	<div class="homeWrap">
		<UserHeader>
			<img src="../../assets/images/ic_chat_white.png" alt="" class="right" slot="right">
			<input type="text"></input>
			<img src="../../assets/images/ic_search_gray.png" class="search">
			<img src="../../assets/img/ic_scan_gray.png" class="scan">
		</UserHeader>

		<MyBanner swipeid='swipe' :autoplay="2000">
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/01.jpg" />
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/02.jpg" />
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/03.jpg" />
			</div>
		</MyBanner>
	
		<!--热点内容  -->
		<Hpage1 :newslist='newslist'></Hpage1>
		<Hpage2 :dblists='dblists'></Hpage2>
		<Hpage3 :weekendlist='weekendlist'></Hpage3>
		<Hpage4 :hfindlist='hfindlist'></Hpage4>
		<Hpage5 :recommendlist='recommendlist'></Hpage5>
	</div>
</template>
<script type="text/javascript">
	import UserHeader from '../.././components/usersHeader.vue'
	import MyBanner from './myBanner.vue'
	import Hpage1 from './hpage1.vue'
	import Hpage2 from './hpage2.vue'
	import Hpage3 from './hpage3.vue'
	import Hpage4 from './hpage4.vue'
	import Hpage5 from './hpage5.vue'
	import request from 'superagent'
	import jsonp from 'superagent-jsonp'
	export default{
		components:{
			UserHeader,MyBanner,Hpage1,Hpage2,Hpage3,Hpage4,Hpage5
		},
		data:function(){
			return {
				newslist:[],
				dblists:[],
				newslists:[],
				weekendlist:[],
				recommendlist:[],
				hfindlist:[]
			}

		},
		mounted:function(){
			var that = this;
			this.axios.get('static/data/myhomeData.json').then(function(res){
				//热点	
				that.newslist = res.data.newslist
				//豆瓣时间
				that.dblists = res.data.dblists
				//为你推荐
				that.recommendlist = res.data.recommendlist
				//周末
				that.weekendlist=res.data.weekendlist
				//发现
				that.hfindlist = res.data.hfindlist
			})
			// request.get("https://api.douban.com/v2/event/list?loc=108288&start=0&count=5")
			// .use(jsonp)
			// .end((err, res) => {
			// 	if(!err){
			// 		console.log(res.body)
			// 		this.hotNews=res.body.events
			// 	}
			// })
		}
	}
</script>

<style type="text/css">
	.homeWrap{background:#F7F7F7}
	.homeWrap #headerWrap{background: #41C054}
	.homeWrap .hpage1_Wrap{background: #FFF}
	.homeWrap .hpage2_Wrap{padding-bottom: .5rem}
	.homeWrap #headerWrap .scan{right: 65px;}
	.homeWrap .search{left: 17px;top: 27px;}

</style>


