<template>
	<div class="hpage2_Wrap clearfix">
		<Hhot title='论坛时间'></Hhot>
		<MyBanner swipeid='swipe07' paginationDirection="right" :pagination=false :autoplay=0 :loop=false slidesPerView="auto">
		  <div v-for='val in dblists'  class="swiper-slide" slot="swiper-con">
	        <Hdbtime :dblist='val'>
	        </Hdbtime>
	      </div>
		</MyBanner>
	</div>	
</template>
<script type="text/javascript">
	import Hhot from '../../components/hjg_hots.vue'
	import MyBanner from './myBanner.vue'
	import Hdbtime from '../../components/hjg_dbtime.vue'
	export default{
		components:{
			Hhot,MyBanner,Hdbtime
		},
		props:{
			dblists:{
				type:Array,
				default:[]
			}
		}
	}
</script>


