<template>
	<div id="tabBar">
		<Item txt='首页' mark='home' :selel=selected @change='getval'>
			<img slot='normalImg' src="../assets/images/ic_tab_home_normal.png" />
			<img slot='activeImg' src="../assets/images/ic_tab_home_active.png" />
		</Item>
		<Item txt='影视' mark='audio' :selel=selected @change='getval'>
			<img slot='normalImg' src="../assets/images/ic_tab_subject_normal.png"/>
			<img slot='activeImg' src="../assets/images/ic_tab_subject_active.png"/>
		</Item>
		<Item txt='博客' mark='broadcast' :selel=selected @change='getval'>
			<img slot='normalImg' src="../assets/images/ic_tab_status_normal.png"/>
			<img slot='activeImg' src="../assets/images/ic_tab_status_active.png"/>
		</Item>
		<Item txt='论坛' mark='group' :selel=selected @change='getval'>
			<img slot='normalImg' src="../assets/images/ic_tab_group_normal.png"/>
			<img slot='activeImg' src="../assets/images/ic_tab_group_active.png"/>
		</Item>
	</div>
</template>

<script type="text/javascript">
	import Item from './item'
	export default {
		data(){
			return{
				selected:'home'
			}
			
		},
		methods:{
			getval:function(val){
				this.selected=val
			}
		},
		components: {
			Item
		}
	}
</script>

<style type="text/css">
	#tabBar{width:100%;height: 64px;position: fixed;left: 0px;bottom: 0px;border-top: 1px solid #eee;background: #F8F9F8;z-index:999}
</style>


