<template>
  <div class="broadheaderWrap">
    <slot name="pic"></slot>
    <div class="text">
      <h3>{{ titles }}</h3>
      <p>{{ contents }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["titles","contents"]
}
</script>

<style scoped>
  .broadheaderWrap {position: relative;margin-left: .36rem;margin-bottom: .4rem}
  .broadheaderWrap img {width: 2.76rem;margin: .7rem 0 0 1.8rem}
  .broadheaderWrap .text {position: absolute;left: 5rem;top: 1.25rem;}
  .broadheaderWrap h3 {line-height: 1rem;font-size: .5rem;color: #494949;}
  .broadheaderWrap p {line-height: .38rem;font-size: .38rem;color: #9b9b9b;}
</style>


