<template>
	<div class="divPageWrap" >
		<h2>{{msg}}</h2>
		<pagechlid :total='totals' :change='fun'></pagechlid>
	</div>
</template>

<script type="text/javascript">
	import Pagechlid from './pageChlid.vue'
	export default {
		components:{
			Pagechlid
		},
		data () {
			return{
//				num:10,
				totals:100,
				msg:'当前页面为:4'
			}
		},
		methods:{
			fun:function(val){
				 this.msg='当前页面为:'+val;
			}
		}

	}
</script>


