<template>
	<div class="items" @click='sel()'>
		<span v-show='!bol'><slot name='normalImg'></slot><br/></span>
		<span v-show='bol'><slot name='activeImg'></slot><br/></span>
		<span class="ac" :class={green:bol}>{{txt}}</span>
	</div>
</template>
<script type="text/javascript">
	export default {
		props:['txt','mark','selel'],
		methods:{
			sel:function(){
				this.$emit('change',this.mark)
				this.$router.push('/'+this.mark)
			}
		},
		computed:{
			bol:function(){
				return this.mark==this.selel?true:false;
			}
		}
	}
</script>
<style type="text/css">
	.items{width: 25%;float: left;text-align: center;font-size:0}
	.items img{width:1.3rem;}
	.items span{font-size: 0.36rem;}
	.items .green{color:#46bd5d}
</style>


