<template>

	<div class="swiper-container clearfix" :class='swipeid'>
		<div class="swiper-wrapper">
			<slot name="swiper-con"></slot>
		</div>
		<div :class="{'swiper-pagination':pagination}" :style='{"text-align":paginationDirection}'></div>
	</div>
</template>
<!-- 轮播swiper的地址 www.swiper.com.cn -->
<script type="text/javascript">
import '../../assets/libs/sliper/js/swiper.min.js'
	export default{
		props:{
			swipeid:{ //ID或class名
				type:String,
				default:''
			},
			effect:{ //轮播切换效果
				type:String,
				default:'slide'
			},
			loop:{ //是否循环
				type:Boolean,
				default:true
			},
			direction:{ //轮播方向
				type:String,
				default:'horizontal' //水平方向vervitity
			},
			autoplay:{ //自动切换时间
				type:Number,
				default:5000
			},
			paginationType:{ //分页器类型
				type:String,
				default:'bullets'
			},
			pagination:{ //分页器
				type:Boolean,
				default:true
			},
			paginationDirection:{ //分页器在哪里显示
				type:String,
				default:'center'
			},
			slidesPerView:{
				type:[Number,String],
				default:1
			},
			loopedSlides: {
		      type: Number,
		      default: 1
		    }
		},
		mounted:function(){ //挂载完成后
			var That = this;
			new Swiper('.'+That.swipeid,{
				loop:That.loop, //是否循环
				pagination:'.swiper-pagination', //分页 参数为类名或者ID名
				paginationType:'That.paginationType', //分页类型
				autoplay:That.autoplay,
				effect:That.effect,
				//用户操作后 不继续轮播 
				autoplayDisableOnInteraction:false,
				slidesPerView:That.slidesPerView,
				loopedSlides:That.loopedSlides,
				paginationDirection:That.paginationDirection,
				direction:That.direction,
				observer:true,
				observevParents:true

			})
		}
	}
</script>
<style>
	@import '../../assets/libs/sliper/css/swiper.min.css';
	.swiper-container img{width: 100%;}
	.swiper-slide {font-size: 0}
  .swiper-container.swipe02 .swiper-slide {width: 5rem;margin-left: .3rem;height:5rem;overflow: hidden;border-radius:.2rem}
  .swiper-container.swipe02 .swiper-slide img{width:100%;height:100%}
  .swiper-container.swipe03 .swiper-slide {width: 2.7rem;margin-left: .45rem;}
  .swiper-container.swipe04 .swiper-slide {width: 3rem;margin-left: .32rem;overflow: hidden;}
  .swiper-container.swipe04 {padding-bottom: .55rem;}
  .swiper-container.swipe05 .swiper-slide {width: 4.8rem;margin-left: .2rem;}
  .swiper-container.swipe06 .swiper-slide {width: 3rem;margin-left: .32rem;}
  .swiper-container.swipe06 {padding-bottom: .55rem;}
  .swiper-container.swipe07 .swiper-slide {width: 7.7rem;margin-left: .3rem;height:7.7rem;overflow: hidden;}
  .swiper-container img {width: 100%;border-bottom: 1px solid #DFDFDF;}
  .swiper-slide {font-size: 0;}
</style>


