<template>
	<div class="audioWrap">
		<UserHeader txt='影视'>
			<img src="../../assets/images/ic_actionbar_search_icon.png" alt="" class="left" slot="left">
			<img src="../../assets/images/ic_chat_green.png" alt="" class="right" slot="right">
		</UserHeader>
		<ul>
			<li :class='{active:nowIndex==1}' @click='nowIndex=1'>
				<router-link to='/audio/film'>电影</router-link>
			</li>
			<li :class='{active:nowIndex==2}' @click='nowIndex=2'>
				<router-link to='/audio/read'>连续剧</router-link>
			</li>
			<li :class='{active:nowIndex==3}' @click='nowIndex=3'>
				<router-link to='/audio/tv'>综艺</router-link>
			</li>
			<li :class='{active:nowIndex==4}' @click='nowIndex=4'>
				<router-link to='/audio/city'>新闻</router-link>
			</li>
		</ul>
		<router-view></router-view>
	</div>
</template>
<script type="text/javascript">
	import UserHeader from '../../components/usersHeader.vue'
	export default{
		components:{
			UserHeader
		},
		data () {
			return{
				nowIndex:1
			}
			
		}
	}
</script>
<style type="text/css">
	.audioWrap ul{height:1.2rem;border-bottom:1px solid #eee;}
	.audioWrap li{width: 25%;float: left;list-style: none;line-height:30px;text-align: center;height:1.19rem}
	.audioWrap a{font-size: .34rem;text-decoration: none;color: #999}
	.audioWrap .active{border-bottom: 1px solid #67c880;color:#67c880;}
	.audioWrap .active a {color: #67c880}
</style>


