<template>
  <div class="hobbyWrap">
    <div v-for='val in grouparr' class="grouplist clearfix">
       <h2 class="title">{{ val.theme }}</h2>   
    <div v-for='item in val.themeitem' class="groupitem clearfix">
    <div class="pic">
      <img :src="item.titImg" />
    </div>
    <div class="mid">
      <h4>{{ item.title }}</h4>
      <p>{{ item.contents }}</p>
    </div>
    <div class="right clearfix">
      <p>{{item.num}}人</p>
        <img v-show='item.bol' @click='item.bol=!item.bol' src="../../assets/images/ic_group_check_anonymous.png" alt=""/>
        <img v-show='!item.bol' @click='item.bol=!item.bol' src="../../assets/images/ic_check_circle_pressed.png" alt=""/>
      </div>
    </div>
  </div>

  </div>
</template>

<script>
export default {
  components: {
  },
  props:{
    grouparr:{
      type:Array,
      default:[]
    }
  }
}
</script>

<style scoped>
  .grouplist{position: relative;padding-bottom: .81rem;border-top: 1px solid #ccc;margin-left: .36rem;width: 11.52rem;}
  .hobbyWrap {}
  .hobbyWrap .title {background: #fff;width: 2.12rem;position: absolute;left: 4.7rem;top: -.3rem;height: .6rem;font-size: .42rem;text-align: center;color: #494949;}

  .groupitem {padding-top: .83rem;padding-bottom: .07rem;}
  .groupitem .pic {float: left;width: 1.45rem;overflow: hidden;margin-top:.2rem;border-radius: .2rem;}
  .groupitem .pic img {width: 1.45rem;float: left;}
  .groupitem .mid {float: left;width: 7rem;padding-left: .32rem;}
  .groupitem .mid h4 {font-size: .48rem;color: #494949;}
  .groupitem .mid p {font-size: .34rem;line-height: .6rem;color: #9b9b9b;overflow:hidden; text-overflow:ellipsis;display:-webkit-box; -webkit-box-orient:vertical;-webkit-line-clamp:2;width:7rem;}
  .groupitem .right {float: right;width: 2.35rem;position: relative;}
  .groupitem .right p {font-size: .34rem;color: #9b9b9b;float: left;padding-top: .6rem;}
  .groupitem .right a {float:right;font-size: 0;margin-top: .5rem;}
  .groupitem .right img {width: .71rem;position: absolute;top:.5rem;right:-.2rem}


</style>


