<template>
	<div class="childWrap">
		<ul>
			<li @click='fn(1)'>首页</li>
			<li @click='fn(chosenPage-1)'>上一页</li>
			<li @click='fn(chosenPage-3)' v-if='chosenPage-3>1'>{{chosenPage-3}}</li>
			<li @click='fn(chosenPage-2)' v-if='chosenPage-2>1'>{{chosenPage-2}}</li>
			<li @click='fn(chosenPage-1)' v-if='chosenPage-1>1'>{{chosenPage-1}}</li>
			<li :class={ac:true}>{{chosenPage}}</li>
			<li @click='fn(chosenPage+1)' v-if='chosenPage+1<total'>{{chosenPage+1}}</li>
			<li @click='fn(chosenPage+2)' v-if='chosenPage+2<total'>{{chosenPage+2}}</li>
			<li @click='fn(chosenPage+3)' v-if='chosenPage+3<total'>{{chosenPage+3}}</li>
			<li @click='fn(chosenPage+1)'>下一页</li>
			<li @click='fn(total)'>尾页</li>
		</ul>
		<input type="number" v-model='pagenum'></input><button type="button" @click='fn(pagenum)'>GO</button>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:['total','change'],
		data () {
			return {
				chosenPage:10,
				pagenum:''
			}
		},
		methods:{
			fn:function(val){
				var pagnum = parseInt(val);
				
				if(!pagnum || pagnum<0 || pagnum>101){
					console.log('输错了')
					return false;
				}

				// if(this.chosenPage<1){
				// 	this.chosenPage=1
				// }
				// if(this.chosenPage>100){
				// 	this.chosenPage=100
				// }
				this.chosenPage=pagnum;
				this.change(this.chosenPage)
			},

		}
	}
</script>
<style type="text/css">
	*{margin:0;padding: 0}
	ul{list-style: none;}
	li{display: inline-block;border:1px solid #eee;margin:10px;padding:5px}
	.ac{background: skyblue}
</style>


