<template id="myheader">
	<div id="headerWrap">
		<span class="txt">{{txt}}</span>
		<slot name='left'></slot>
		<slot name='right'></slot>
		<slot></slot>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:['txt']
	}
</script>
<style type="text/css">
	#headerWrap{width: 100%;height: 44px;line-height:44px;background: #eee;text-align: center;position: relative;font-weight: bold;}
	#headerWrap span{font-size: 0.5rem;color: #424242}
		#headerWrap img{width:25px;position: absolute;top: 10px;}
		#headerWrap .left{right:80px;}
		#headerWrap .right{right: 20px;}
		#headerWrap input{width: 80%;height: 30px;text-align: left;position: absolute;left: 15px;top: 8px;border-radius: 5px;border: none;background-color: #fff;text-indent: 3em;}
</style>


