<template>
  <div class="hotWrap clearfix">
    <!-- 左边绿色竖线 -->
    <span class="soll" v-show='issoll' :class='{bgcolor:bgcolor}'></span>
    <!--标题-->
    <h3 :class="{red:color}">{{ title }}</h3>
    <!-- 右边栏目 -->
    <span :class='classnames' v-show='bol'>{{must}}></span>
    <span v-show='explain' class="explain">豆瓣网友制作的电影榜单</span>
  </div>
</template>

<script>
  export default {
    props:{
      title:{
        type:String,
        default:''
      },
      color:{
        type:Boolean,
        default:false
      },
      bgcolor:{
        type:Boolean,
        default:false
      },
      bol:{
        type:Boolean,
        default:false
      },
      must:{
        type:String,
        default:"更多栏目"
      },
      issoll:{
        type:Boolean,
        default:true
      },
      classnames:{
        type:String,
        default:'column'
      },
      explain:{
        type:Boolean,
        default:false
      }
    },
    methods: {
    }
  }
</script>

<style scoped>
.hotWrap{position: relative;height:0.74rem;width: 100%;padding:0.4rem 0}
.hotWrap span.soll{width:0.2rem;height:.74rem;background:#41C054;display:block;position: absolute;left: 0;top:0.4rem}
.hotWrap h3 {line-height: .74rem;font-size: .6rem;padding-left: .62rem;width: 7.7rem;color: #494949;}
.hotWrap span.column{line-height: .74rem;font-size: .36rem;color:#A4A4A4;position: absolute;right:0.62rem;top:0.45rem;text-align: center}
.hotWrap .red{color:#FF8448}
.hotWrap span.bgcolor{background:#FF8448}
.hotWrap span.green{line-height: .74rem;font-size: .36rem;position: absolute;right:0.62rem;top:0.45rem;text-align: center;color:#45BE58}
.hotWrap span.explain{font-size:.36rem;position: absolute;left:.55rem;color:#D5D5D5}
</style>


