<template>
	<div class="hjgdetailswrap">
			<img :src="news.urls">
			<h3>标题：{{news.title}}</h3>
			<p>摘要：{{news.des}}</p>
			<span>{{news.writer}}</span>


<!-- 			"title":"周末去哪儿·北京|在夏日出行的列车上，一切丧气都会被迎风吹散",
					"des":"六月北京正式开启高温模式，24℃三级冷风模式的空调+100%的手机电量+满格的信号",
					"writer":"赵丽颖",
					"urls":"/static/img/hjgtou8.png",
					"id":7 -->
	</div>
</template>
<script type="text/javascript">
	import request from 'superagent'
	import jsonp from 'superagent-jsonp'
	export default{
		data:function(){
			return{
				news:{}
			}
		},
		created:function(){
			var id=this.$route.params.id;
			var that = this;
			//请求home里的数据	
			if(id>=0 && id<=17){	
				this.axios.get('static/data/myhomeData.json').then(function(res){
					
					res.data.newslist.forEach(function(item){
						if(id==item.id){
							that.news = item;
							return
						}
					});
					res.data.recommendlist.forEach(function(item){
						if(id==item.id){
							that.news = item;
							return
						}
					});
					res.data.weekendlist.forEach(function(item){
						if(id==item.id){
							that.news = item;
							return
						}
					});
					console.log(that.news)
				})
			};
			//请求电影里的数据
			if(id>=1000 && id<=1010){
				this.axios.get('static/data/myfilmData.json').then(function(res){
					//请求filmpage6（最受欢迎影评的数据）
					if(id<=1002){
						res.data.gdliketvlist.forEach(function(item){
							if(id==item.id){
								that.news = item;
							}
						})
					}
					//请求filmpage8的数据
					if(id>1002 && id<=1005){
						res.data.findtvlist.forEach(function(item){
							if(id==item.id){
								that.news = item;
							}
						})
					}
				})
			}
		}
	}
	
</script>
<style type="text/css">
	.hjgdetailswrap img{width:8rem;height:8rem;margin:2.2rem;margin-bottom:1rem;margin-top:.5rem}
	.hjgdetailswrap h3{font-size:.56rem;padding:.3rem .6rem}
	.hjgdetailswrap p{font-size:.4rem;padding:.3rem .6rem}
	.hjgdetailswrap span{float: right;font-size:.34rem;padding:.3rem .6rem}
</style>


