<template>
  <div class="recomitemWrap">
    <div class="title clearfix">
      <img :src="dblist.ur" alt=""/>
    </div>
    <div class="content">
      <h4>{{ dblist.title }}</h4>
      <p>共{{ dblist.filedate }}期</p>
      <div class="icon clearfix">
        <img :src="dblist.portrait" alt=""/>
        <h5>{{ dblist.name }}</h5>
        <p>{{ dblist.introduce }}</p>
      </div>
    </div>
    <a href="javascript:;" class="bottom">查看专栏</a>
  </div>
</template>

<script>
  export default {
    props:{
      dblist:{
        type:Object,
        default:function(){
          return{
            "title":"古今：杨照史记百讲",
            "filedate":"100",
            "name":"Mr黄爵",
            "introduce":"简单心理创始人，心理咨询师，心理领域科普作者",
            "ur":"",
            "portrait":""
          } 
        }
      }
    }
  }
</script>

<style scoped>
  .recomitemWrap {width: 8.08rem;height: 7.35rem;background: #fff;border-radius: .15rem;margin-left: .13rem;overflow: hidden;padding-bottom: .64rem;}
  .recomitemWrap .title {width: 8.08rem;height: 2.71rem;}
  .recomitemWrap .title img {width: 100%;height: 100%;float: left;}
  .recomitemWrap .content {width: 7.2rem;padding-left: .44rem;padding-bottom: .38rem;}
  .recomitemWrap .content h4 {font-size: .42rem;color: #494949;height: .66rem;line-height: .66rem;padding-top: .27rem;}
  .recomitemWrap .content p {font-size: .3rem;color: #9b9b9b;height: .56rem;line-height: .56rem;padding-bottom: .17rem;}
  .recomitemWrap .icon{position: relative;}
  .recomitemWrap .icon img {width: 1.2rem;height: 1.2rem;border-radius: 50%;float: left;}
  .recomitemWrap .icon h5 {font-size: .34rem;color: #494949;margin-left: 1.48rem;}
  /*超出隐藏，显示省略号，强制一行，显示16个字节*/
  .recomitemWrap .icon p {margin-left: 1.48rem;overflow: hidden;white-space: nowrap;text-overflow:ellipsis;width: 16em;position: absolute;left: 0;bottom:-.3rem;}
  .recomitemWrap .bottom {display: block;width: 7.2rem;height: .94rem;border-radius: 8px;border: 1px solid #43bd57;font-size: .34rem;color: #43bd57;text-align: center;line-height: .94rem;margin-left: .3rem}
</style>


