<template>
	<div class="filmDisWrap clearfix" :class="{filmpage5:fmpg5}">
		<slot name='showimg'></slot>
		<h4>{{filmName}}</h4>
		<div class="rate">
			<slot name='yi'></slot>
			<slot name='er'></slot>
			<slot name='san'></slot>
			<slot name='si'></slot>
			<slot name='wu'></slot>
			<slot name='liu'></slot>
			<span :class={color:color}>{{score}}</span>
		</div>
	</div>
</template>

<script>
	export default {
		props:['filmName','score','color','fmpg5']
	}
</script>
	
<style>
	.filmDisWrap{width: 4.8rem;background: #fff}
	.filmDisWrap .showimg{width: 100%;float: left;}
	.filmDisWrap h4{font-size: .34rem;text-align: left;}
	.filmDisWrap span{font-size: .4rem;}
	.filmDisWrap .rate img{width:.5rem;border:none;float: left;}
	.filmDisWrap span.color{color:#A7A7A7}
	.filmpage5{width:3.4rem;display: inline-block;margin-left: .38rem;padding:.3rem 0}
	.filmpage5 span{vertical-align:top}
</style>


