<template>
  <div  class="broadinnerwrap clearfix">
    <div class='brodinnerlist clearfix' v-for='(val,index) in innerlist'>
      <div class="icon">
        <img :src="val.portraiturl" class="pic"/>
      <img src="../../assets/images/rating_star_card_on.png" alt="" class="pic1"/>
        <slot name="icon"></slot>
      </div>
      <div class="mid">
        <a href="javascript:;" class="follow" @click="val.follownum++">关注</a>
        <!-- 标题 -->
        <h3>{{ val.title }}</h3>
        <!-- 关注人数 -->
        <p>{{ val.follownum }}人关注</p>
        <!-- 副标题 -->
        <h4>{{ val.contents  }}</h4>
        <!-- 二维码 -->
        <div class="msgpic">
          <img v-show='val.isshow' :src="val.erweima"/>
        </div>

        <div class="btn clearfix">
        <!-- 赞 留言数量 -->
          <span class="fabulous">
          <img @click='val.goodnum++' src="../../assets/images/ic_like_full.png" alt="">
          {{ val.goodnum }}</span>
          <span class="step">
         <img @click='val.msgnum++' src="../../assets/images/ic_comment_full.png" alt="">
          {{ val.msgnum }}</span>
        </div>
      </div>
      <i class="fork" @click='del(index)'>×</i>
    </div>
  </div>
</template>

<script>
export default {
  props:{
    innerlist:{
      type:Array,
      default:[]
    }
  },
  methods:{
    del:function(index){
      this.innerlist.splice(index,1)
    }
  }
}
</script>

<style scoped>
  .brodinnerlist{margin:.38rem;background:#fff;border-radius:.2rem;width: 11.7rem;}
  .brodinnerlist .icon {width: 1.5rem;position: relative;padding: .54rem .38rem 0 .45rem;float: left;}
  .brodinnerlist .icon .pic {width: 1.5rem;border-radius: 50%}
  .brodinnerlist .icon .pic1 {display: block;width: .37rem;height: .37rem;overflow: hidden;border: .04rem solid #fff;border-radius: 50%;position: absolute;right: .38rem;top: 1.6rem;background: #fff;}
  .brodinnerlist .mid {padding-top: .54rem;position: relative;float: left;width: 8.6rem;}
  .brodinnerlist .mid .follow {position: absolute;display: block;right: 0;top: .87rem;width: 2.25rem;height: .84rem;color: #fff;background: #42bd56;line-height: .84rem;text-align: center;font-size: .36rem;border-radius: .1rem;}
  .brodinnerlist .mid h3 {height: .7rem;line-height: .7rem;font-size: .48rem;color: #494949;padding-top: .14rem;}
  .brodinnerlist .mid p {height: .58rem;line-height: .58rem;font-size: .36rem;color: #9b9b9b;}
  .brodinnerlist .mid h4 {line-height: .6rem;font-size: .44rem;color: #494949;font-weight: normal;width: 8.6rem;margin: .24rem 0;}
  .brodinnerlist .mid .msgpic {width: 8.58rem;text-align: center;font-size: 0;}
  .brodinnerlist .mid .msgpic img {width: 100%;}
  .brodinnerlist .mid span {display: block;color: #ccc;font-size: .36rem;padding:0 .3rem 0 .74rem;float: left;height: 1.5rem;line-height: 1.5rem;position: relative;}
  .brodinnerlist .mid span img {width: .44rem;height: .44rem;position: absolute;left: .12rem;top: .5rem;}
  .brodinnerlist .fork {color: #ccc;font-size:.8rem;}
</style>


