import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Botbar from '../components/botbar'
import Page from '../components/fyparend.vue'


import Home from '../pages/home/home.vue'
import Audio from '../pages/audio/audio.vue'
import Group from '../pages/group/group.vue'
import Broadcast from '../pages/broadcast/broadcast.vue'
import Details from '../pages/details/hjgdetails.vue'
// audio的二级菜单
import  Read from '../pages/audio/read.vue'
import  Tv from '../pages/audio/tv.vue'
import  City from '../pages/audio/city.vue'
import  Music from '../pages/audio/music.vue'
import  Film from '../pages/audio/film.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    { //首页
      path: '/home',
      name: 'Home',
      component: Home
    },
    { //书影音
      path: '/audio',
      name: 'Audio',
      component: Audio,
      children:[ //二级路径路由
        {path:'/',component:Film},
        {path:'film',component:Film},
        {path:'read',component:Read},
        {path:'tv',component:Tv},
        {path:'city',component:City},
        {path:'music',component:Music},
      ]
    },
    { //小组
      path: '/group',
      name: 'Group',
      component: Group
    },
    { //广播
      path: '/broadcast',
      name: 'Broadcast',
      component: Broadcast
    },
    { //我的
      path: '/Details',
      name: 'Details',
      component: Details
    }
  ]
})



