<template>
	<div class="groupWrap">
		<UserHeader txt='专家论坛'>
			<img src="../../assets/images/ic_actionbar_search_icon.png" alt="" class="left" slot="left">
			<img src="../../assets/images/ic_chat_green.png" alt="" class="right" slot="right">
		</UserHeader>
		<Grouppage1></Grouppage1>
		<Grouppage2 :grouparr='grouparr'></Grouppage2>
		<Repbatch></Repbatch>
	</div>
</template>
<script type="text/javascript">
	import UserHeader from '../../components/usersHeader.vue'
	import Grouppage1 from './groppage1.vue'
	import Grouppage2 from './grouppage2.vue'
	import Repbatch from '../../components/hjg_repbatch.vue'
	export default{
		components:{
			UserHeader,Grouppage1,Grouppage2,Repbatch
		},
		data () {
			return{
				grouparr:[]
			}
		},
		mounted:function(){
			var that = this;
			this.axios.get('static/data/mygroupData.json').then(function(res){
				console.log(res.data)
				that.grouparr = res.data.grouparr

			})
		}

	}
</script>
<style type="text/css">
	.groupWrap{width: 100%;}
</style>


